:root {
    --main-bg-color: #FAFAFA;
    --main-text-color: #333;
    --max-width: 800px;
}
body, header, main, footer, h2, h3, ul, li, button {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    background-color: var(--main-bg-color);
    color: var(--main-text-color);
    font-family: Geneva, Tahoma, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
}
li {
    list-style-type: none;
}
#root {
    padding: 10px;
    margin-bottom: 60px;
}