.input-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 10px;
    text-align: center;
}
.input-container__text {
    width: calc(100% - 110px);
}
.input-container__input {
    margin-left: 10px;
    background-color: var(--main-bg-color);
    color: var(--main-text-color);
    border: 1px solid var(--main-text-color);
    padding-left: 5px;
    height: 30px;
    width: 100px;
    font-size: 1.1em;
}