.controls {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 20px;
    left: 0;
    width: 100%;
    opacity: 0.5;
}
.controls:hover, .controls:active {
    opacity: 1;
}
.controls li {
    margin-right: 15px;
}
.controls li:last-child {
    margin-right: 0;
}
.controls__circle {
    box-sizing: border-box;
    width: 25px;
    height: 25px;
    border: 2px solid var(--main-text-color);
    border-radius: 50%;
}
.controls__circle--current {
    background-color: var(--main-text-color);
}
.controls .checkbox-container {
    margin-top: 0;
}
.controls .checkbox-container__checkbox {
    width: 25px;
    height: 25px;
}